#main {
	display: flex;
	flex-direction: column;

	@media (max-width: 1050px) {
		align-items: center;
	}
}

#up {
	position: fixed;
	right: 15px;
	bottom: 25px;
	opacity: 0;
	transform: scale(0.7);
	animation: up_slide 0.6s 0.8s ease forwards;

	@media (max-width: 700px) {
		background: $c-background;
		padding: 10px;
		right: 5px;
		bottom: 5px;
		transform: scale(0.5);
	}
}

@keyframes up_slide {
	0% {
		opacity: 0;
		transform: scale(0.6) translateY(1rem);
	}

	100% {
		opacity: 0.8;
		transform: scale(0.6) translateY(0rem);
	}
}
